<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods' }">商品列表</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <!-- 提示信息 -->
      <el-alert title="添加用户" :closable="false" type="info" center show-icon></el-alert>
      <!-- 进度条 -->
      <el-steps :space="200" :active="activeIndex-0" finish-status="success" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- tab页 -->
      <el-form :model="addGoodsForm" :rules="addGoodsRules" ref="addGoodsRef" label-position="top">
        <el-tabs tab-position="left" v-model="activeIndex" :before-leave="preventSwitch" @tab-click="clickTabs">
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addGoodsForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addGoodsForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addGoodsForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addGoodsForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader v-model="addGoodsForm.goods_cat" :options="goodsClassifyData" :props="cascaderProps" @change="cascaderChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品参数" name="1">
            <el-form-item :label="item.attr_name" v-for="(item,i) in manyData" :key="i">
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="item" v-for="(item,i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <el-form-item :label="item.attr_name" v-for="(item,i) in onlyData" :key="i">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!-- action:图片上传的服务器地址 -->
            <!-- headers:upload组件内部封装了ajax请求，并不是通过axios发送，
            所以请求拦截器上设置的请求头对其无效，需要自定义请求头，发送token -->
            <!-- on-success：图片成功上传之后的操作 -->
            <el-upload :headers="requestHeaders" :on-success="handleSuccess" :action="uploadsImg" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <quill-editor v-model="addGoodsForm.goods_introduce"></quill-editor>
            <el-button type="primary" class="addGoodsBtn" @click="addGoodsSure">添加商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!-- dialog--图片预览 -->
    <el-dialog title="图片预览" :visible.sync="previewImgVisible">
      <el-image style="width: 100%; height: 100%" :src="previewImgPath" fit="fill"></el-image>
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data() {
    return {
      // 进度条进度、tab页位置
      activeIndex: '0',

      // 表单数据
      addGoodsForm: {
        goods_name: '', // 名称
        goods_price: '', // 价格
        goods_number: '', // 数量
        goods_weight: '', // 重量
        goods_cat: [], // 级联选择选中的分类数组信息
        pics: [], // 图片路径
        goods_introduce: '', // 商品介绍
        attrs: [], // 商品的动态参数和静态属性
      },
      // 表单验证
      addGoodsRules: {
        goods_name: [
          { required: true, message: '请输入商品名称', trigger: 'blur' },
        ],
        goods_price: [
          { required: true, message: '请输入商品价格', trigger: 'blur' },
        ],
        goods_number: [
          { required: true, message: '请输入商品数量', trigger: 'blur' },
        ],
        goods_weight: [
          { required: true, message: '请输入商品重量', trigger: 'blur' },
        ],
        goods_cat: [
          { required: true, message: '请添加分类信息', trigger: 'blur' },
        ],
      },

      // 商品分类信息(全部)
      goodsClassifyData: [],
      // 级联选择器配置选项
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover',
      },

      // 商品分类参数
      manyData: [],
      // 商品分类属性
      onlyData: [],

      // 图片上传的地址
      uploadsImg: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 图片发起请求时，设置的请求头对象
      requestHeaders: {
        Authorization: window.sessionStorage.getItem('token'),
      },
      // 预览图片的dialog显示与隐藏
      previewImgVisible: false,
      // 预览图片的路径
      previewImgPath: '',
    }
  },
  methods: {
    // 获取商品分类列表数据
    async getGoodsClassifyData() {
      let { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200)
        return this.$message.error('获取商品分类信息失败!')
      this.goodsClassifyData = res.data
    },

    // 选中级联选择器的某个值
    async cascaderChange() {
      if (this.addGoodsForm.goods_cat.length !== 3) {
        this.addGoodsForm.goods_cat = []
        return
      }
      console.log(this.addGoodsForm.goods_cat)
      // 获取manyData值
      let { data: res1 } = await this.$http.get(
        `categories/${this.getGoodsClassifyID}/attributes`,
        {
          params: {
            sel: 'many',
          },
        }
      )
      if (res1.meta.status !== 200)
        return this.$message.error('获取商品分类参数失败!')
      res1.data.filter((item) => {
        item.attr_vals = item.attr_vals.split(' ')
      })
      this.manyData = res1.data

      // 获取onlyData值
      let { data: res } = await this.$http.get(
        `categories/${this.getGoodsClassifyID}/attributes`,
        {
          params: {
            sel: 'only',
          },
        }
      )
      if (res.meta.status !== 200) return this.$message('获取商品属性失败!')
      this.onlyData = res.data

      // 这里的form表单的分类值是一个数组，提交的时候需要转成API可接收的字符串
      // console.log(this.addGoodsForm.goods_cat)
    },

    // 判断使否阻止标签页切换
    preventSwitch(activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addGoodsForm.goods_cat.length !== 3) {
        this.$message.error('请选择商品分类！')
        return false
      }
    },

    // 点击标签页按钮
    clickTabs() {
      // console.log(this.activeIndex)
      if (this.activeIndex === '1') {
        // console.log(this.manyData)
      } else if (this.activeIndex === '2') {
      }
    },

    // 点击选择的图片
    handlePreview(file) {
      // console.log(file)
      this.previewImgPath = file.response.data.url // 获取图片的实际存放路径
      this.previewImgVisible = true
    },
    // 移除选中的图片
    handleRemove(file) {
      // console.log(file)
      const picInfo = file.response.data.tmp_path // 1、从file中找到临时存放路径
      const index = this.addGoodsForm.pics.findIndex((x) => x.pic === picInfo) // 2、根据这个路径找到数组中对应的索引值
      this.addGoodsForm.pics.splice(index, 1) // 3、根据splice删除元素
    },
    // 图片成功上传到服务器临时路径之后的操作
    handleSuccess(response) {
      // console.log(response)
      let picInfo = { pic: response.data.tmp_path } // 1、封装成api需要的pics数据格式
      this.addGoodsForm.pics.push(picInfo) // 2、添加到表单数据pics中
    },

    // 添加商品(最终)
    addGoodsSure() {
      this.$refs.addGoodsRef.validate(async (valid) => {
        if (!valid) return this.$message.error('请将表单信息填写完整后提交！')
        // 直接将表单数据中的goods_cat转换成字符串会报错,因为级联选择器的默认选中项(v-model)需要是一个数组,所以要将表单数据进行深拷贝
        // 第1种方式：lodash.cloneDeep(obj)
        // let addGoodsFormDeep = _.cloneDeep(this.addGoodsForm)
        // addGoodsFormDeep.goods_cat = addGoodsFormDeep.goods_cat.join(',')
        // console.log(addGoodsFormDeep)
        // 第3种方式：JSON.parse(JSON.stringify(obj))
        let addGoodsFormDeep = JSON.parse(JSON.stringify(this.addGoodsForm))
        addGoodsFormDeep.goods_cat = addGoodsFormDeep.goods_cat.join(',')

        // 处理动态参数
        this.manyData.forEach((item) => {
          let manyObj = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' '),
          }
          addGoodsFormDeep.attrs.push(manyObj)
        })
        // 处理静态属性
        this.onlyData.forEach((item) => {
          let onlyObj = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals,
          }
          addGoodsFormDeep.attrs.push(onlyObj)
        })
        console.log(addGoodsFormDeep)

        // 发送添加商品的请求
        let { data: res } = await this.$http.post('goods', addGoodsFormDeep)
        console.log(res)
        if (res.meta.status === 400) return this.$message.error(res.meta.msg)
        if (res.meta.status !== 201)
          return this.$message.error('添加商品失败！')
        this.$message.success('添加商品成功！')
        this.$router.push('/goods')
      })
    },
  },

  computed: {
    // 封装函数--获取商品分类id
    getGoodsClassifyID() {
      if (this.addGoodsForm.goods_cat.length === 3) {
        return this.addGoodsForm.goods_cat[2]
      }
    },
  },
  created() {
    this.getGoodsClassifyData()
  },
}
</script>

<style lang="less" scoped>
.el-steps,
.el-tabs {
  margin-top: 20px;
}
.el-tabs {
  min-height: 300px;
}
// 复选框
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
// 添加商品按钮
.addGoodsBtn {
  margin-top: 20px;
}
</style>